<template>
	<view>
		<view style="position: sticky; top: 0; z-index: 2;">
			<view class="tabBar flex-center">
				<view class="tabBar-child flex-center" :class="{ active: isActive }" @click="changeTab">发现</view>
				<view class="tabBar-child flex-center " :class="{ active: !isActive }" @click="changeTab">使用说明</view>
			</view>
			<view class="lable">{{ notice.content }}</view>
			<view class="search-box"><input type="text" placeholder="请输入关键词" class="input-box" /></view>
		</view>
		<view class="item-row" v-for="(item, index) in items" :key="index" v-if="isActive">
			<view class="avatar"><image :src="item.cover" class="avatar" /></view>
			<view class="content">
				<view class="share-title">{{ item.title }}</view>
				<view class="content-title">{{ item.summary.substring(0, 25) }}……</view>
			</view>
			<view class="score">
				<view class="content-title">{{ item.price }}积分</view>
				<view class="content-title" v-if="item.downloadUrl == null">兑换</view>
				<view class="content-title" v-else>下载</view>
			</view>
		</view>  
		<view class="use-box" v-if="!isActive">
			<view style="font-size: 14px;">
				资源均为免费，兑换后可查看下载地址;点击我的->兑换，即可查看、下载兑换过的资源。
			</view>
			<view style=" font-size:15px ;font-weight: 700;">积分获得方式</view>
			<view style="padding-left: 26upx; font-size: 14px;">>每日签到</view>
			<view style="padding-left: 26upx; font-size: 14px;">>投稿</view>
			<view style="padding-left: 26upx; font-size: 14px;">>转发</view> 
			<view style="padding-left: 26upx; font-size: 14px;">>提建议、意见(尚未完成)</view>
			
		</view>
	</view>
</template> 

<script>
import { request, get } from '@/utils/request';
import { SHARE_URL, NOTICE_URL,BASE_API_URL } from '@/utils/api';
export default {
	data() {
		return {
			isActive: true,
			items: [],
			notice: {}
		};
	},
	onLoad() {
		this.getShare(),
		this.getNotices()
	
	},
	methods: {
		changeTab() {
			this.isActive = !this.isActive;
		},
		getShare: function() {
			console.log('>>>>>>>>>>>>>>>>>>>>>>>请求')
			uni.request({
				url: BASE_API_URL+'/shares/query',
				method: 'GET', 
				header: {
					'content-type': 'application/json',
					'X-Token': uni.getStorageSync('token') 
				},
				success: res => {
					console.log('进入回调函数');
					console.log(">>>>>>>>>>>>>>>>chcbsjbsj"+res.data)
					this.items = res.data.data;
					uni.setStorageSync("items",res.data.data)
				}
			});
		}, 
		getNotices: function() { 
			let self = this;
			console.log(">>>>>>>>>>>>>>>>>进入函数")
			uni.request({ 
				url: BASE_API_URL+'/notices/latest',
				method: 'GET',
				header: {     
					'content-type': 'application/json',
					'X-Token': uni.getStorageSync('token')
				},
				success: res => {
					console.log(res.data)
					self.notice = res.data.data;
					console.log('>>>>>>>>>>>>>>>..' + self.notice);
				}
			});
		}
	}
};
</script>

<style scoped>
.tabBar {
	background-color: #ffffff;
	height: 80upx;
}
.tabBar-child {
	height: 100%;
	width: 50%;
	font-size: 16px;
	color: #8c8c8c;
}
.flex-center {
	display: flex;
	justify-content: center;
	align-items: center;
}
.flex-around {
	display: flex;
	justify-content: space-around;
	align-items: center;
}
.active {
	color: black;
	font-weight: 400;
	border-bottom: #fe4543 solid 2px;
}
.lable {
	display: flex;
	align-items: center;
	height: 60upx;
	background-color: #fefce5;
	font-size: 12px;
	font-weight: 400;
	color: #e4883b;
}
.search-box {
	height: 45upx;
	background-color: #f2f2f2;
	padding: 15upx;
}
.input-box {
	height: 25upx;
	padding: 4upx;
	background-color: #ffffff;
	font-size: 12px;
	color: #727272;
	width: 95%;
}
.item-row {
	height: 160upx;
	padding: 20upx;
	display: flex;
	justify-content: space-around;
	border-bottom: 1px solid #dfdfdf;
}
.avatar {
	width: 120upx;
	height: 120upx;
	border-radius: 50%;
}
.content {
	width: 60%;
	display: flex;
	flex-direction: column;
}
.score {
	width: 15%;
	display: flex;
	flex-direction: column;
}
.share-title {
	font-weight: 600;
	font-size: 14px;
}
.content-title {
	font-size: 13px;
}
.use-box{
	display: flex;
	flex-direction: column;
}
.tips-box{
	
}
</style>
